<template>
  <div class="study2">
    <h2>{{title}}</h2>
    <section class="study2-ls">
      <div v-for="(item, i) in album" :key="i">
        <img :src="item.src" alt="">
      </div>
    </section>
    <div class="study2-pagination">
      <span ontouchstart="" v-show="this.currentPage < this.totalPages" @click="loadMore">Load more...</span>
    </div>
  </div>
</template>

<script>
import { $fetch } from '@/store'

const DOMAIN = 'https://jrainlau.com/api/study/imgs/detail'

export default {
  data () {
    return {
      href: '',
      title: '',
      album: [],
      currentPage: 1
    }
  },
  computed: {
    totalPages () {
      const eg = this.album[0]
      let total = 0
      if (eg) {
        total = eg.totalPages
      }
      return total
    }
  },
  mounted () {
    document.body.querySelector('header').style.display = 'none'
    document.title = 'Study hard'
    const { href, title } = this.$route.query
    this.href = href.replace(/^[/](.*)[/]$/g, '$1')
    this.title = title
    this.getDetail()
  },
  methods: {
    async getDetail (index = 1) {
      const result = await $fetch({
        url: DOMAIN,
        method: 'post',
        data: {
          domain: localStorage.getItem('study_keyword'),
          href: this.href,
          index
        }
      }).catch(e => e.response)
      this.album = this.album.concat(result.data)
    },
    loadMore () {
      this.currentPage++
      if (this.currentPage <= this.totalPages) {
        console.log(this.currentPage, this.totalPages)
        this.getDetail(this.currentPage)
      }
    }
  }
}
</script>

